
.sidebar {
  background: #303030 url("../assets/images/misty-mountains-bw.png") no-repeat center top;
  background-position: -30px -90px;
  background-size: auto 290px;
  color: #a9a9a9;
  position: fixed;
  top: 0px;
  right: 0px;
  bottom: 0px;
  overflow: auto;
  box-sizing: border-box;
  padding: 180px 40px 60px 40px;
  width: 100%;
  box-shadow: inset 10px 0px 20px -10px rgba(0,0,0,0.5);
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  transition: all 0.3s ease-out;
  font-size: 14px;
  opacity: 0;
  pointer-events: none;
}
@media (min-width: 768px) {
.sidebar {
    max-width: 50vw;
}
}
.sidebar.active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
  pointer-events: all;
}
.sidebar .toggle-sidebar {
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  height: 150px;
  color: #a9a9a9;
  cursor: pointer;
}
.sidebar .toggle-sidebar .material-icons {
  position: absolute;
  right: 20px;
  top: 10px;
  font-size: 24px;
  line-height: 40px;
  display: none;
  color: #f5f5f5;
}
.sidebar a {
  color: #a9a9a9;
}
.sidebar p {
  margin: 5px auto;
}
.sidebar h4 {
  font-size: 16px;
  line-height: 24px;
  font-weight: 300;
  margin: 50px auto 10px auto;
  color: #f5f5f5;
}
.sidebar h4 a {
  color: inherit;
}
.sidebar h4 i {
  line-height: 1;
  vertical-align: top;
  display: inline;
  position: relative;
  top: 2px;
}
.sidebar select {
  height: 24px;
  border: 0px;
  margin: 0px;
  width: 75%;
  cursor: pointer;
  color: #d4d4d4;
  background-color: #4f4f4f;
}
.sidebar select:focus {
  background-color: #f8f8ff;
  color: #333;
}
.sidebar .layout {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.sidebar .layout .fieldset {
  width: 100%;
}
@media (min-width: 480px) {
.sidebar .layout {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.sidebar .layout .fieldset {
    width: 50%;
}
}
.sidebar label[disabled] {
  opacity: 0.5;
  cursor: text;
  pointer-events: none;
}
.sidebar .copyrights {
  font-size: 12px;
  margin-top: 60px;
}
.sidebar .copyrights div * + * {
  margin-left: 0.5em;
}
.sidebar .profile {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.sidebar .profile .avatar {
  height: 60px;
  display: block;
  cursor: pointer;
  border-radius: 25%;
  opacity: 0.75;
}
.sidebar .profile .name {
  font-size: 24px;
  margin-top: 15px;
  color: #f5f5f5;
}
.sidebar .import-export {
  margin-top: 50px;
  width: 100%;
}
.sidebar .import-export .info {
  display: none;
}
.sidebar .import-export .switch-menu-wrapper {
  margin-bottom: 20px;
  border-bottom-color: rgba(220,220,220,0.1);
  margin-left: -5px;
  width: calc(100% + 10px);
}
.sidebar .import-export .switch-menu.type a {
  font-size: 16px;
  padding-left: 5px;
  padding-right: 5px;
}
.sidebar .import-export .switch-menu a {
  color: rgba(169,169,169,0.5);
}
.sidebar .import-export .switch-menu a:hover:after {
  background-color: rgba(169,169,169,0.5);
}
.sidebar .import-export .switch-menu a.active {
  color: #f5f5f5;
}
.sidebar .import-export .switch-menu a.active:after {
  background-color: #a9a9a9;
}
.sidebar .import-export textarea {
  background-color: transparent;
  color: #a9a9a9;
  resize: none;
  height: 10em !important;
  padding: 0px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.sidebar .import-export .submit {
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.sidebar .import-export .submit .button {
  font-size: 12px;
  padding: 5px 20px;
  font-weight: 400;
}
.sidebar .import-export .submit .button + .button {
  margin-left: 10px;
}
.sidebar .import-export .submit .merge {
  color: #333;
}


