}
.long-click {
  position: fixed;
  -webkit-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  left: 50%;
  top: 50%;
  width: 50px;
  height: 50px;
  pointer-events: none;
}
.long-click .mask {
  width: 50%;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 50%;
  top: 0px;
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-animation: mask-swap steps(2, end) 0.375s;
          animation: mask-swap steps(2, end) 0.375s;
}
.long-click .spinner {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  overflow: hidden;
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-animation: pie-rotation 0.375s linear;
          animation: pie-rotation 0.375s linear;
}
.long-click .spinner:after {
  content: ' ';
  position: absolute;
  left: -100%;
  top: 0px;
  width: 200%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 50%;
  border: solid rgba(51,51,51,0.1) 10px;
}
.long-click .spinner-clone {
  width: 50%;
  height: 100%;
  position: absolute;
  left: 50%;
  top: 0px;
  overflow: hidden;
  -webkit-animation: pie-swap steps(1, end) 0.375s;
          animation: pie-swap steps(1, end) 0.375s;
  opacity: 1;
}
.long-click .spinner-clone:after {
  content: ' ';
  position: absolute;
  left: -100%;
  top: 0px;
  width: 200%;
  height: 100%;
  box-sizing: border-box;
  border-radius: 50%;
  border: solid rgba(51,51,51,0.1) 10px;
}
@-webkit-keyframes pie-rotation {
0% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
49.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
99.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@keyframes pie-rotation {
0% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
49.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
50% {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
}
99.999% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
}
@-webkit-keyframes pie-swap {
0% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@keyframes pie-swap {
0% {
    opacity: 0;
}
50% {
    opacity: 1;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes mask-swap {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes mask-swap {
0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
