
.api-debug .method:after {
  content: "/";
  font-weight: 500;
  margin-left: 10px;
}
.api-debug .url {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.api-debug input[type="text"] {
  border: 0;
  padding: 0;
  background-color: transparent;
  font-weight: 500;
  box-shadow: 0px 1px 0px 0px;
  outline: none;
}
.api-debug input[type="text"]::-webkit-input-placeholder {
  color: #c0c0c0;
}
.api-debug input[type="text"]:-ms-input-placeholder {
  color: #c0c0c0;
}
.api-debug input[type="text"]::placeholder {
  color: #c0c0c0;
}
.api-debug input[type="text"].error {
  text-align: center;
  box-shadow: 0px 1px 0px 0px #f15a22;
}
.api-debug .send {
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.api-debug .send button + button {
  margin-left: 10px;
}
.api-debug .response {
  overflow-x: hidden;
  margin: 40px 0px;
}
.api-debug .response.error {
  color: #f15a22;
}
.api-debug .response pre {
  margin: 0;
}


