
.batch-actions {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  line-height: 24px;
}
.batch-actions .label {
  color: titamota-color-text-muted;
}
.batch-actions .icon-button {
  cursor: pointer;
  margin-right: 0.375em;
  margin-left: 0.375em;
  font-size: 20px;
  position: relative;
  top: -2px;
}
.batch-actions .icon-button:first-child {
  margin-left: 0em;
}


