
article {
  font-size: 18px;
  line-height: 1.5;
  padding-top: 40px;
  padding-bottom: 40px;
}
article[chibi="3"]:after {
  padding-bottom: 60vh;
  content: ' ';
  display: block;
  position: absolute;
  bottom: 0px;
  left: 0px;
  right: 0px;
  height: 75vh;
  background-image: url("../assets/images/chibi-03.png");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: auto 100%;
}
article h3 {
  margin-top: 40px;
  color: #f15a22;
}
article blockquote h3 {
  margin-top: 20px;
}


