}
[color] {
  position: relative;
}
[color]:before {
  content: ' ';
  position: absolute;
  left: -1em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 0.5em;
  height: 0.5em;
  border-radius: 30%;
  background-color: #dcdcdc;
}
[color="0"]:before {
  background-color: #72cff9;
}
[color="1"]:before {
  background-color: #7d89cd;
}
[color="2"]:before {
  background-color: #71c5bd;
}
[color="3"]:before {
