.icon-button {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.icon-button i.material-icons {
  font-size: inherit;
  line-height: inherit;
  display: block;
}
.icon-button.lg {
  font-size: 24px;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
}
.icon-button input[type="checkbox"] {
