  height: 1em;
  width: 1em;
  margin: 0;
  display: block;
}
article {
  font-size: 18px;
  max-width: 40em;
}
.switch-menu-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: solid 1px rgba(220,220,220,0.75);
}
.switch-menu-wrapper .switch-menu {
  line-height: 24px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  margin-bottom: 10px;
  text-align: right;
}
.switch-menu-wrapper .switch-menu a + a {
  margin-left: 5px;
}
.switch-menu-wrapper .switch-menu a {
  padding: 0 3px;
  cursor: pointer;
  font-size: 13px;
  display: inline-block;
  position: relative;
}
.switch-menu-wrapper .switch-menu a:after {
  position: absolute;
  left: 0;
  bottom: -2px;
  height: 3px;
  display: block;
  width: 100%;
  background-color: #dcdcdc;
}
.switch-menu-wrapper .switch-menu a:hover:after {
  content: ' ';
}
.switch-menu-wrapper .switch-menu a.active:after {
  content: ' ';
  background-color: #333;
}
@media (min-width: 768px) {
.switch-menu-wrapper {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.switch-menu-wrapper .switch-menu {
    margin-bottom: 0;
}
}
label {
  cursor: pointer;
}
.custom-checkbox {
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  position: relative;
}
.custom-checkbox input {
  display: block;
  cursor: pointer;
  opacity: 0;
  position: absolute;
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  font-size: 1em;
  z-index: 2;
}
.custom-checkbox .material-icons {
  display: block;
  font-size: 18px;
  margin: 0;
  z-index: 1;
  position: relative;
}
.custom-checkbox input:not(:checked) ~ .material-icons.on {
  display: none;
}
.custom-checkbox input:not(:checked) ~ .material-icons.off {
  display: block;
}
.custom-checkbox input:checked ~ .material-icons.on {
  display: block;
}
.custom-checkbox input:checked ~ .material-icons.off {
  display: none;
}
.custom-checkbox + span {
  margin-left: 0.5em;
  vertical-align: top;
  display: inline-block;
  position: relative;
  top: 1px;
}
.api-debug .method:after {
