
.task-context {
  font-size: 14px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.task-context .name {
  white-space: nowrap;
  overflow: hidden;
  color: #c0c0c0;
  text-overflow: ellipsis;
}
.task-context .clear,
.task-context .back {
  pointer-events: none;
  visibility: hidden;
  cursor: pointer;
  transition: all 0.3s;
}
.task-context .clear.active,
.task-context .back.active {
  pointer-events: all;
  visibility: visible;
}
.task-context .back {
  margin-right: 0.375em;
  margin-top: 0.2em;
  -ms-flex-item-align: start;
      align-self: flex-start;
  color: #333;
}
.task-context .clear {
  vertical-align: middle;
  margin-left: 0.375em;
  color: #333;
}


