
.modal-wrapper {
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  position: fixed;
  overflow: auto;
  background-color: rgba(48,48,48,0);
}
.modal-wrapper .modal-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}


