
.group-name .link-url {
  opacity: 0.5;
  margin-right: 1em;
}
.group-name .link-name + .link-url {
  display: block;
  font-size: 87.5%;
}
.group-name .favicon {
  width: 1em;
  height: 1em;
  display: inline-block;
  vertical-align: top;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 0.375em;
  position: relative;
  top: 0.125em;
}
.group-name .favicon.undefined {
  display: none;
}
.group-name .favicon.github {
  background-image: url("../assets/images/github.svg");
}
.group-name .favicon.basecamp {
  background-image: url("../assets/images/basecamp.svg");
}
.group-name .favicon.asana {
  background-image: url("../assets/images/asana.png");
}
.group-name .favicon.trello {
  background-image: url("../assets/images/trello.svg");
}
.group-name .favicon.bitbucket {
  background-image: url("../assets/images/bitbucket.svg");
}
.group-name .favicon.atlassian {
  background-image: url("../assets/images/atlassian.svg");
}


