
.choose-avatar {
  width: 320px;
  text-align: center;
}
@media (min-width: 768px) {
.choose-avatar {
    width: 480px;
}
}
@media (min-width: 1024px) {
.choose-avatar {
    width: 45em;
}
}
.choose-avatar ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 20px auto;
  padding: 0px;
  list-style: none;
  max-height: 75vh;
  overflow: auto;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.choose-avatar li {
  padding: 0;
  text-align: center;
  margin-bottom: 20px;
  width: 30%;
  font-size: 12px;
  font-weight: 400;
}
@media (min-width: 768px) {
.choose-avatar li {
    width: 25%;
}
}
@media (min-width: 1024px) {
.choose-avatar li {
    width: 20%;
}
}
.choose-avatar li img {
  display: block;
  margin: 0 auto;
  height: 50px;
  margin-bottom: 5px;
}


