
.import-export {
  width: 320px;
}
@media (min-width: 768px) {
.import-export {
    width: 480px;
}
}
.import-export nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.import-export nav > * {
  margin: 0;
}
.import-export nav a {
  cursor: text;
}
.import-export .submit {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.import-export .submit[disabled] {
  opacity: 0.5;
  pointer-events: none;
  cursor: text;
}
.import-export .submit .button {
  margin-bottom: 10px;
  text-align: center;
}
@media (min-width: 768px) {
.import-export .submit {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
.import-export .submit .button {
    margin-bottom: 0px;
}
}
.import-export [view="import"] .toggle-export,
.import-export [view="export"] .toggle-import {
  text-decoration: underline;
  color: #c0c0c0;
  cursor: pointer;
}
.import-export textarea {
  display: block;
  border: none;
  font-size: 12px;
  line-height: 16px;
  font-family: PT Mono, monospace;
  height: 50vh;
  width: 100%;
  padding: 5px 10px;
  box-sizing: border-box;
  border-radius: 5px;
}
.import-export .import textarea {
  height: calc(50vh - 63px);
}


